import * as React from 'react';
import { RedocStandalone } from 'redoc';
import { Slice } from 'gatsby';
import { Box } from '@chakra-ui/react';

const ADAPDocumentation = ({
  location,
  currentVersion,
  versions,
  currentRepo,
}) => {
  return (
    <Box>
      <Slice
        location={location}
        currentVersion={currentVersion}
        versions={versions}
        currentRepo={currentRepo}
        alias="header"
      />
      <RedocStandalone
        specUrl={'https://stoplight.io/api/v1/projects/radiantlogic/adap/nodes/reference/ADAP.yaml'}
        options={{
          nativeScrollbars: true,
          theme: {
            rightPanel: {
              backgroundColor: '#3D3D3E',
            },
            panels: {
              backgroundColor: '#000!important',
              title: {
                color: '#3D3D3E!important',
              },
            },
            codeBlock: {
              // backgroundColor: "#3D3D3E",
              borderRadius: '15px',
            },
            sidebar: {
              backgroundColor: '#474747',
              textColor: 'white',
            },
            schema: {
              typeNameColor: '#7B61FF',
              linesColor: 'transparent',
            },
          },
        }}
      />
      <Slice alias="footer" />
    </Box>
  );
};

export default ADAPDocumentation;
